export function refundStatusFilter(value) {
  switch (value) {
    case 'APPLY_PENDING':
      return ''
    case 'CLOSED':
      return ''
    case 'REFUSED':
      return ''
    case 'CONFIRMED':
      return ''
    case 'LACK_TRANSFER_CONDITION':
      return ''
    case 'PENDING_TRANSFER_ACCOUNT':
      return ''
    case 'FAIL_TRANSFER_ACCOUNT':
      return ''
    case 'SUCCESS_MANUAL':
      return '()'
    case 'SUCCESS_AUTO':
      return '()'
    default:
      throw new Error(`: ${value}`)
  }
}

export function refundReasonFilter(value) {
  switch (value) {
    case 'misdiagnose':
      return ''
    case 'user_asked':
      return ''
    case 'other':
      return ''
    default:
      throw new Error(`: ${value}`)
  }
}
