/**
 * idArr.join(',')
 * @param {String} id
 * @param {[String]} params.refund_ids //  _id
 */
export function getBatchRefundList(params) {
  return request({
    url: '/order_refund/batch/check',
    method: 'get',
    params
    // mock: '/order_refund/batch/check'
  })
}

